@import 'styles/variables-p44.scss';

.legend {
  background-color: rgba($chathams-blue, 0.7);
  color: $white;
  font-family: $font-family-base; // overrides mapbox's included font-family
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 9999;
}

.legendInner {
  align-items: center;
  display: flex;
  height: 65px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 8px;
  width: 1140px;
}

.legendIcons {
  align-items: center;
  display: flex;
  margin-left: 15px;
  text-transform: uppercase;
}

.title {
  @include toRem(font-size, 21px);
}
